<template>
  <div id="page-body">
    <!-- 输入框-->
    <div id="input-back">
      <inputArea @submit = 'handleSubmit'></inputArea>
    </div>
   <!--图片显示框-->
    <div id = 'img-holder'>
        <imgDisplayer id='img-box' :imgs='imgs' :selected = 'selected' @select-img ='select_img'></imgDisplayer>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import inputArea from './inputArea'
import imgDisplayer from './imgDisplayer'
axios.defaults.baseURL = 'http://' + document.domain + ':8000'

export default {
  name: 'home',
  data: function () {
    return {
      imgs: [],
      selected: 0
    }
  },
  components: {
    inputArea,
    imgDisplayer,
  },
  mounted: function () {
    var basePath = '../../static/images/'
    var format = '.png'
    var fileName = ['icon']
    for (let name of fileName) {
      this.imgs.push(basePath + name + format)
    }
  },
  methods:{
    select_img(index){
      console.log("slect " + index)
      this.selected = index
    },
    scroll_left(){
      var div = document.getElementById('small-img-holder')
      setTimeout(function(){
        div.scrollLeft = div.scrollWidth
      }, 50)
    },
    insert_img(url){
      console.log(url)
      this.imgs.push(url)
      this.selected = this.imgs.length-1
      this.scroll_left()
    },
    handleSubmit: function (data) {
      //     var basePath = '../../static/images/'
      // var format = '.jpg'
      // var fileName = ['123']
      // this.insert_img(basePath + fileName +format)
      const datajson = JSON.stringify(data)
      console.log(axios.defaults.baseURL)
      axios.get('/picture/pic', {
        params: {
          content: datajson
        }
      }).then(this.handleReturn)
      .catch(function(e){
        alert("错误：" + e);
      })
    },
    handleReturn (res) {
      var img =  'data:image/jpeg;base64,' + res.data
      this.insert_img(img)
    }
  }
}
</script>

<style>
html {
  margin: 0;
  background: url('../../static/images//background.jpg');
  background-size: 1920px 1080px;
  background-repeat:no-repeat;
}
body {
  background: none;
  }

#page-body {
  overflow: hidden;
  position: absolute;
  display: block;
  border: 1rem solid rgba(100, 100, 100, 0.5);
  height: 40rem;
  width: 60rem;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%); 
  }

#input-back {
  overflow: auto;
  width: 30%;
  height: 100%;
  max-height: 100%;
  display: flex;
  align-items: center;
  float: left;
  background-color: #3bb4f1;
  background-image: url('../../static/images//bg.png');
  background-repeat: no-repeat;
}

#img-holder {
  float: right;
  overflow: hidden;
  height: 100%;
  width: 70%;
  display: flex;
  align-items: center;
  background-color: white;
}

#img-box {
  width: 100%;
  max-height: 80%;
  overflow: auto;
}

</style>



