<template>
    <div class="inputWrapper">
        <div class="color-box">
          <div name="red" :class="['red', red==1?'wrapper-active':'wrapper-negative']"  @click="red = (red+1)%2"/>
          <div name="orange" :class="['orange', orange==1?'wrapper-active':'wrapper-negative']"  @click="orange = (orange+1)%2"/>
          <div name="yellow" :class="['yellow', yellow==1?'wrapper-active':'wrapper-negative']"  @click="yellow = (yellow+1)%2"/>
          <div name="green" :class="['green', green==1?'wrapper-active':'wrapper-negative']"  @click="green = (green+1)%2"/>
          <div name="cyan" :class="['cyan', cyan==1?'wrapper-active':'wrapper-negative']"  @click="cyan = (cyan+1)%2"/>
          <div name="blue" :class="['blue', blue==1?'wrapper-active':'wrapper-negative']"  @click="blue = (blue+1)%2"/> 
          <div name="purple" :class="['purple', purple==1?'wrapper-active':'wrapper-negative']"  @click="purple = (purple+1)%2"/>
          <div name="gray" :class="['gray', gray==1?'wrapper-active':'wrapper-negative']"  @click="gray = (gray+1)%2"/>
          <div name="pink" :class="['pink', pink==1?'wrapper-active':'wrapper-negative']"  @click="pink = (pink+1)%2"/>
          <div name="black" :class="['black', black==1?'wrapper-active':'wrapper-negative']"  @click="black = (black+1)%2"/>
          <div name="white" :class="['white', white==1?'wrapper-active':'wrapper-negative']"  @click="white = (white+1)%2"/>
          <div name="brown" :class="['brown', brown==1?'wrapper-active':'wrapper-negative']"  @click="brown = (brown+1)%2"/>
        </div>
      <div class="inputScope">
        <label for="pattern">样式：</label>
        <input
          type="number"
          name="pattern"
          class="form-control"
          min="0"
          max="63"
          v-model="scope"
          placeholder="0~63"
        />
      </div>
      <button class="small button" @click="handleInput">提交</button>
  </div>
</template>

<script>
export default {
  name: 'InputArea',
  data () {
    return {
      red: 0,
      orange: 0,
      yellow: 0,
      green: 0,
      cyan: 0,
      blue: 0,
      purple: 0,
      gray: 0,
      pink: 0,
      black: 0,
      white: 0,
      brown: 0,
      scope: null
    }
  },
  methods: {
    handleInput: function () {
      if(! this.scope){
        alert("请输入样式参数。");
      }
      else if (this.scope >= 0 && this.scope <= 63) {
        var color12 = this.red + ',' +
        this.orange + ',' + this.yellow + ',' + this.green +
        ',' + this.cyan + ',' + this.blue + ',' + this.purple +
        ',' + this.gray + ',' + this.pink + ',' + this.black +
        ',' + this.white + ',' + this.brown
        this.$emit('submit',{ 
            color12: color12,
            scope: this.scope
            })
      } else {
        alert('请确保数值在0~63的范围')
        this.scope = this.scope > 63 ? 63 : 0;
      }
    }
  }
}
</script>

<style>
  .wrapper {
    border-radius: 0.125rem;
  }
  .red{
    background-color: red;
  }
  .orange{
    background-color: orange;
  }
  .yellow{
    background-color: yellow;
  }
  .green{
    background-color: green;
  }
  .cyan{
    background-color: cyan;
  }
  .blue{
    background-color: blue;
  }
  .purple{
    background-color: purple;
  }
  .gray{
    background-color: gray;
  }
  .pink{
    background-color: pink;
  }
  .black{
    background-color: black;
  }
  .white{
    background-color: white;
  }
  .brown{
    background-color: brown;
  }
  .inputWrapper {
    width: 100%;
  }
  .color-box {
    /* padding: 10px; */
    display: flex;
    flex-wrap: wrap;
    width: 80%;
    /* height: 10rem; */
    /* background-color: rgba(105, 105, 105, 0.5); */
    margin-left: 50%;
    transform: translate(-50%, 0);
  }
  .wrapper-negative {
    margin: 3px;
    width: 32px;
    height: 32px;
  }
  .wrapper-active {
    margin: 3px;
    width: 28px;
    height: 28px;
    border: 2px solid rgb(78, 236, 118);
    border-radius: 5px;
  }

  
.button.small{
  padding: 4px 12px;
}

.button:active{
  background: #e9e9e9;
  position: relative;
  top: 1px;
  text-shadow: none;
  -moz-box-shadow: 0 1px 1px rgba(0, 0, 0, .3) inset;
  -webkit-box-shadow: 0 1px 1px rgba(0, 0, 0, .3) inset;
  box-shadow: 0 1px 1px rgba(0, 0, 0, .3) inset;
}

.button:hover{
  background-color: #eee;
  color: #555;
}

/* 按钮样式 */
.button{
  display: inline-block;
  *display: inline;
  zoom: 1;
  padding: 6px 20px;
  margin: 0;
  cursor: pointer;
  border: 1px solid #bbb;
  overflow: visible;
  font: bold 13px arial, helvetica, sans-serif;
  text-decoration: none;
  white-space: nowrap;
  
  background-color: #ddd;
  background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(255,255,255,1)), to(rgba(255,255,255,0)));
  background-image: -webkit-linear-gradient(top, rgba(255,255,255,1), rgba(255,255,255,0));
  background-image: -moz-linear-gradient(top, rgba(255,255,255,1), rgba(255,255,255,0));
  background-image: -ms-linear-gradient(top, rgba(255,255,255,1), rgba(255,255,255,0));
  background-image: -o-linear-gradient(top, rgba(255,255,255,1), rgba(255,255,255,0));
  background-image: linear-gradient(top, rgba(255,255,255,1), rgba(255,255,255,0));
  
  -webkit-transition: background-color .2s ease-out;
  -moz-transition: background-color .2s ease-out;
  -ms-transition: background-color .2s ease-out;
  -o-transition: background-color .2s ease-out;
  transition: background-color .2s ease-out;
  background-clip: padding-box; /* Fix bleeding */
  -moz-border-radius: 3px;
  -webkit-border-radius: 3px;
  border-radius: 3px;
  -moz-box-shadow: 0 1px 0 rgba(0, 0, 0, .3), 0 2px 2px -1px rgba(0, 0, 0, .5), 0 1px 0 rgba(255, 255, 255, .3) inset;
  -webkit-box-shadow: 0 1px 0 rgba(0, 0, 0, .3), 0 2px 2px -1px rgba(0, 0, 0, .5), 0 1px 0 rgba(255, 255, 255, .3) inset;
  box-shadow: 0 1px 0 rgba(0, 0, 0, .3), 0 2px 2px -1px rgba(0, 0, 0, .5), 0 1px 0 rgba(255, 255, 255, .3) inset;
  text-shadow: 0 1px 0 rgba(255,255,255, .9);
  
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

/* 输入框样式 */
.form-control { 
  display: inline-block; 
  margin-top: 1.5rem;
  margin-bottom: 1rem;
  padding-left: 0.125rem;
  width: 3rem; 
  height: 1rem; 
  font-size: 14px; 
  line-height: 1.428571429; 
  color: #555555; 
  background-color: #ffffff; 
  border: 1px solid #cccccc; 
  border-radius: 4px; 
  -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075); 
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075); 
  -webkit-transition: border-color ease-in-out .15s, box-shadow ease-in-out .15s; 
  transition: border-color ease-in-out .15s, box-shadow ease-in-out .15s; 
} 

.form-control:focus { 
  border-color: #66afe9; 
  outline: 0; 
  -webkit-box-shadow: inset 0 1px 1px rgba(0,0,0,.075), 0 0 8px rgba(102, 175, 233, 0.6); 
  box-shadow: inset 0 1px 1px rgba(0,0,0,.075), 0 0 8px rgba(102, 175, 233, 0.6); 
} 
</style>



